import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

export default function Item() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Paper className={classes.paper}><marquee><h1>Buy and Sell in Africa</h1></marquee></Paper>
        </Grid>
        <Grid item xs={6}>
          <Paper className={classes.paper}>East African Products</Paper>
        </Grid>
        <Grid item xs={6}>
          <Paper className={classes.paper}>West African Products</Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>Ethiopian Products</Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>Eritrean Products</Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>Nigerian Products</Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>Ghanian Products</Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>Kenyan Products</Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>Djiboutian Products</Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>Angolian Products</Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>Liberian Products</Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>Sudanese Products</Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>Ugandian Products</Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>Gambian Products</Paper>
        </Grid>
        <Grid item xs={3}>
          <Paper className={classes.paper}>Malian Products</Paper>
        </Grid>
      </Grid>
    </div>
  );
}
